/* jshint ignore:start */

define('tomsterdance/config/environment', ['ember'], function(Ember) {
  var prefix = 'tomsterdance';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("tomsterdance/tests/test-helper");
} else {
  require("tomsterdance/app")["default"].create({"name":"tomsterdance","version":"0.0.0.0d512313"});
}

/* jshint ignore:end */
